export default {
	data() {
		return {
			swiperArr: [],
			swiperImageArr: [],
			dotStyle: false,
			cardCur: 0,
			list: [],
			imageList: [],
			list2: [],
			imageList2: [],
			msg: []
		};
	},
	onLoad() {
		this.selectNotice();
		this.selectAd();
		this.selectSelfProducts();
		this.selectCurrPopularProducts();
	},
	onPullDownRefresh() {
		this.selectNotice();
		this.selectAd();
		this.selectSelfProducts();
		this.selectCurrPopularProducts();
	},
	methods: {
		preview(index, type) {
			var urls = [];
			if (type === 'swiper') {
				urls = this.swiperImageArr;
			}
			if (type === 'list') {
				urls = this.imageList;
			}
			if (type === 'list2') {
				urls = this.imageList2;
			}
			uni.previewImage({
				current: index,
				loop: true,
				indicator: 'default',
				urls: urls
			});
		},
		toMore(type) {
			uni.navigateTo({
				url: `../productList/productList?type=${type}`
			});
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current;
		},
		//公告
		async selectNotice() {
			const res = await this.$util.request({
				requestUrl: 'api/notices'
			});
			console.log('公告：', res);
			if (res !== undefined) {
				this.msg = res.data.data;
			} else {
				uni.stopPullDownRefresh();
			}
		},
		//广告位
		async selectAd() {
			const res = await this.$util.request({
				requestUrl: 'api/ads'
			});
			console.log('广告：', res);
			if (res !== undefined) {
				this.swiperArr = res.data.data;

				this.swiperImageArr = [];
				for (let i of this.swiperArr) {
					this.swiperImageArr.push(i.image);
				}
			} else {
				uni.stopPullDownRefresh();
			}
		},
		//本店
		async selectSelfProducts() {
			const res = await this.$util.request({
				requestUrl: 'api/products',
				data: {
					limit: 6,
					page: 1,
					name: null,
					pType: 1,
					online: 1
				}
			});
			console.log('本店：', res);
			if (res !== undefined) {
				this.list = res.data.data;

				this.imageList = [];
				for (let i of this.list) {
					this.imageList.push(i.image);
				}
			} else {
				uni.stopPullDownRefresh();
			}
		},
		//流行
		async selectCurrPopularProducts() {
			const res = await this.$util.request({
				requestUrl: 'api/products',
				data: {
					limit: 6,
					page: 1,
					name: null,
					pType: 0,
					online: 1
				}
			});
			uni.stopPullDownRefresh();
			console.log('流行：', res);
			if (res !== undefined) {
				this.list2 = res.data.data;

				this.imageList2 = [];
				for (let i of this.list2) {
					this.imageList2.push(i.image);
				}
			}
		}
	}
};
